import { INITIALISE, ADD_ITEM_TO_WORKFLOW } from "./constants";
import { workflowData } from "../../data/workflow";
import { getData, storeData } from "../../utils/common";

export function initializeAction() {
    return (dispatch) => {
        let data = getData("workflow");
        if (!data) {
            storeData("workflow", "[]");
            data = [];
        } else {
            data = JSON.parse(data);
        }
        dispatch(initializeSuccess(data));
    }
}

export function initializeSuccess(data) {
    return {
        type: INITIALISE,
        data
    }
}

export function addItemToWorkflowAction(position, item) {
    return (dispatch, getState) => {
        let workflow = getState().workflow.workflow;

        if (item.actionType === 2) {
            let arr1 = workflow.slice(0, position);
            let arr2 = workflow.slice(position);
            item.leftSide = arr2;
            item.rightSide = [];
            arr1.push(item);
            workflow = arr1;
        } else {
            workflow.splice(position, 0, item);
        }

        storeData("workflow", JSON.stringify(workflow));
        dispatch(AddItemToWrokflowSuccess(workflow));
    }
}

export function AddItemToWrokflowSuccess(data) {
    return {
        type: ADD_ITEM_TO_WORKFLOW,
        data: data
    }
}

export function resetWorkFlowAction(data) {
    storeData("workflow", "[]");
    return {
        type: ADD_ITEM_TO_WORKFLOW,
        data: []
    }
}