import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Node from '../../components/DroppableComponent ';
import { initializeAction, addItemToWorkflowAction, resetWorkFlowAction } from "./action";

class Workflow extends Component {

  componentDidMount() {
    this.props.initializeAction();
  }

  getNodeCard = (node) => {
    return (
      <div class="workflow_card --blue --with-icon">
        <div class="bubble" data-popover="popover" data-content="
              <div class='workflow_actions'>
                <ul class='list-inline'>
                  <li><a href='#' class='workflow-action' data-tooltip='tooltip' data-placement='top' title='Share'><i class='icon icon-share-2'></i></a></li>
                  <li><a href='#' class='workflow-action' data-tooltip='tooltip' data-placement='top' title='View'><i class='icon icon-document-text'></i></a></li>
                  <li><a href='#' class='workflow-action' data-tooltip='tooltip' data-placement='top' title='Delete'><i class='icon icon-trash'></i></a></li>
                </ul>
              </div>
              ">
          <i class={node.icon}></i>
          <p>{node.label}</p>
          <i class="icon-drag icon-resize-plus-2"></i>
        </div>
      </div>
    );
  }

  getNode = (id, hasPrevNode = false, hasNextNode = false) => {
    return (
      <li>
        <div class={"workflow_card--add" + (!hasPrevNode ? " --start " : " ") + (!hasNextNode ? " --end " : " ")}>
          <Node id={id} addItem={this.props.addItemToWorkflowAction}>
            <i class='icon icon-plus'></i>
          </Node>
        </div>
      </li>
    );
  }

  getInnerUl = (data) => {
    return (
      <ul className="flow_inner">
        {this.getWorkFlow(data)}
      </ul>
    );
  }

  getInnerWorkFlow = (node, index, length) => {
    return (
      <React.Fragment>
      <li>
        <ul className="flow_inner">
          {this.getNode(index, index !== 0, index !== length)}
          <li>{this.getNodeCard(node)}</li>
          {index === length - 1 && this.getNode(length, true)}
        </ul>
      </li>
        {node.actionType === 2 && this.getSplitWorkFlow(node, index, length)}
      </React.Fragment>
    );
  }

  getSplitWorkFlow = (node, index, length) => {
    return (
      <li>
        <ul class="flow_split">
         <li>
           <div class="split-left">
             <svg width="100%" height="100%" viewBox="0 0 100 100" preserveAspectRatio="none">
               <path d="M 0,100 C 0,0 100,100 100,0"></path>
             </svg>
           </div>
           {this.getInnerUl(node.leftSide, true)}
         </li>

         <li>
           <div class="split-right">
             <svg width="100%" height="100%" viewBox="0 0 100 100" preserveAspectRatio="none">
               <path d="M 100,100 C 100,0 0,100 0,0"></path>
             </svg>
           </div>
           {this.getInnerUl(node.rightSide, true)}
         </li>
       </ul>
      </li>
    );
  }

  getWorkFlow = (data = []) => {
    if(data.length === 0) {
      return this.getNode(0, false, false);
    }else{
      return (
        <React.Fragment>
          {data.map((node, index) => this.getInnerWorkFlow(node, index, data.length))}
        </React.Fragment>
      );
    }
  }
  
  render() {
    let { workflow} = this.props;
    return (
      <section class="hl_wrapper nav-shrink d-flex">
        <section class="hl_wrapper--inner workflow" id="workflow">
          <section class="hl_workflow--main">
            <ul class="flow_main">
              <li>
                <ul class="flow_start">
                  <li onClick={this.props.resetWorkFlowAction}>
                    <a class="workflow_card--new" data-toggle="tooltip" data-placement="bottom"
                      title="Add new way to start this automation.">
                      <div class="bubble" data-toggle="modal" data-target="#new-automation-modal">
                        <p>Reset Workflow</p>
                      </div>
                    </a>
                  </li>
                  <li>
                    <div class="workflow_card">
                      <div class="bubble" data-popover="popover" data-content="
                          <div class='workflow_actions'>
                            <ul class='list-inline'>
                              <li><a href='#' class='workflow-action' data-tooltip='tooltip' data-placement='top' title='Share'><i class='icon icon-share-2'></i></a></li>
                              <li><a href='#' class='workflow-action' data-tooltip='tooltip' data-placement='top' title='View'><i class='icon icon-document-text'></i></a></li>
                              <li><a href='#' class='workflow-action' data-tooltip='tooltip' data-placement='top' title='Delete'><i class='icon icon-trash'></i></a></li>
                            </ul>
                          </div>
                          ">
                        <p><strong>RSS feed http://www.activecampaign.com/blog/ is</strong> updated and contact has tag Blog
                          subscriber</p>
                      </div>
                    </div>
                  </li>
                  <li>
                    <a class="workflow_card--new" data-toggle="tooltip" data-placement="bottom"
                      title="Add new way to start this automation.">
                      <div class="bubble" data-toggle="modal" data-target="#new-automation-modal">
                        <p>Add new trigger</p>
                      </div>
                    </a>
                  </li>
                </ul>
              </li>

              {workflow && this.getWorkFlow(workflow)}

            </ul>
          </section>
        </section>
      </section>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
      workflow: state.workflow.workflow
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
      initializeAction,
      addItemToWorkflowAction,
      resetWorkFlowAction
  }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(Workflow);
