import { INITIALISE, ADD_ITEM_TO_WORKFLOW } from './constants';

const initialState = {
    workflow: []
};

export default function workflowReducer(state = initialState, action) {
    switch (action.type) {
        case INITIALISE:
            return Object.assign({}, {}, {
                workflow: action.data
            });

        case ADD_ITEM_TO_WORKFLOW:
            return Object.assign({}, {}, {
                workflow: [...action.data]
            });

        default: return state;
    }
}